.authInput {
    max-width: 320px;
    width: 100%;
    padding: 10px;
    border-radius: 7px;
    background-color: rgba(255, 255, 255, 1);
    margin-bottom: 10px;
    font-size: 12px;
    color: black;
}

.authSubmit {
    padding: 10px 0;
    border-radius: 7px;
    width: 100%;
    text-align: center;
    background: #04aaff;
    color: white;
    margin-top: 10px;
    cursor: pointer;
}

.authError {
    color: tomato;
    text-align: center;
    font-weight: 500;
    font-size: 12px;
}

.authSwitch {
    color: #04aaff;
    cursor: pointer;
    margin-top: 10px;
    margin-bottom: 50px;
    display: block;
    font-size: 12px;
    text-decoration: underline;
}
